/* Global Styles */
:root {
  --primary-color: #1e22aa;
  --primary-light: #f3f4ff;
  --accent-green: #d8fa50;
  --accent-dark-green: #9dce78;
  --white: #ffffff;
  --gray-light: #f1f2ff;
  --gray-medium: #cccce9;
  --gray-dark: #0101893e;
  --text-color: #0101893e;
  --text-dark: #1e22aa;
}

body {
  font-family: 'Inter', sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--white);
  color: var(--text-color);
}

.landing-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header Styles */
.header {
  width: 100%;
}

.navigation-bar {
  width: 100%;
  height: 100px;
  padding: 0 32px;
  display: flex;
  align-items: center;
}

.nav-container {
  max-width: 1376px;
  margin: 0 auto;
  background-color: var(--white);
  border-radius: 24px;
  padding: 22px 26px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
}

.logo {
  height: 32px;
  display: flex;
  align-items: center;
}

.logo-image {
  height: 100%;
}

.nav-links {
  flex: 1;
  margin: 0 310px;
}

.nav-menu {
  display: flex;
  list-style: none;
  gap: 42px;
  margin: 0;
  padding: 0;
}

.nav-item {
  display: flex;
}

.nav-link {
  color: var(--primary-color);
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  opacity: 0.93;
  transition: opacity 0.3s ease;
}

.nav-link:hover {
  opacity: 1;
}

.nav-link.active {
  opacity: 1;
  font-weight: 600;
}

.nav-buttons {
  display: flex;
  gap: 12px;
}

/* Button Styles */
.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 14px 24px;
  border-radius: 14px;
  border: none;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  gap: 10px;
}

.btn-lg {
  padding: 23px 24px;
  border-radius: 24px;
  font-size: 16px;
}

.btn-primary {
  background: linear-gradient(45deg, var(--accent-green), var(--accent-dark-green));
  color: var(--white);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.btn-outline {
  background-color: var(--gray-light);
  color: var(--primary-color);
  border: 1px solid var(--gray-medium);
}

.btn-outline:hover {
  background-color: var(--white);
}

.btn-block {
  width: 100%;
}

.btn-arrow {
  width: 16px;
  height: 16px;
  position: relative;
}

.btn-arrow::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 5.5px;
  width: 5px;
  height: 10px;
  border-right: 1.4px solid currentColor;
  border-bottom: 1.4px solid currentColor;
  transform: rotate(-45deg);
}

/* Hero Section Styles */
.hero-section {
  padding: 42px 75px;
  background: linear-gradient(135deg, var(--primary-light), var(--white));
  min-height: 736px;
}

.content-container {
  display: flex;
  max-width: 1280px;
  margin: 0 auto;
  gap: 75px;
}

/* Hero Content Styles */
.hero-content {
  flex: 1;
  max-width: 647px;
  display: flex;
  flex-direction: column;
  gap: 56px;
}

.tagline-banner {
  display: flex;
  align-items: center;
  gap: 12px;
  height: 46px;
  padding: 4px;
  border-radius: 500px;
}

.icon-circle {
  width: 38px;
  height: 38px;
  background-color: var(--white);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.award-icon {
  width: 16px;
  height: 16px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.award-icon::before,
.award-icon::after {
  content: "";
  position: absolute;
  background: transparent;
  border: 1.4px solid var(--primary-color);
}

.award-icon::before {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  top: 2px;
  left: 4px;
}

.award-icon::after {
  width: 5.7px;
  height: 5.9px;
  transform: rotate(45deg);
  top: 8px;
  left: 5px;
}

.tagline {
  color: var(--text-dark);
  font-size: 19px;
  font-weight: 500;
}

.hero-text-container {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.hero-title {
  font-size: 72px;
  line-height: 1.2;
  font-weight: 700;
  color: var(--text-dark);
  margin: 0;
}

.hero-description {
  font-size: 18px;
  line-height: 1.6;
  color: var(--text-dark);
  opacity: 0.93;
  max-width: 500px;
  margin: 0;
}

.hero-buttons {
  display: flex;
  gap: 16px;
}

/* Hero Illustration Styles */
.hero-illustration {
  flex: 1;
  position: relative;
  min-height: 600px;
}

.pattern-bg {
  position: absolute;
  width: 512px;
  height: 256px;
  background: linear-gradient(135deg, var(--primary-light), var(--white));
  border-radius: 24px;
  left: 185px;
  top: 274px;
  z-index: 0;
}

.calculator-card {
  position: absolute;
  width: 519px;
  height: 427px;
  background-color: var(--white);
  border-radius: 32px;
  top: 130px;
  left: 63px;
  padding: 24px 16px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  z-index: 2;
}

.card-header {
  color: var(--primary-color);
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
}

.calculator-content {
  background-color: var(--white);
  border-radius: 24px;
  box-shadow: 18px 20px 60px rgba(30, 10, 82, 0.06);
  padding: 8px;
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.filter-options {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.filter-row {
  display: flex;
  gap: 12px;
}

.filter-option {
  flex: 1;
  height: 44px;
  background-color: var(--gray-light);
  border-radius: 24px;
  display: flex;
  align-items: center;
  padding: 8px 16px;
  gap: 8px;
}

.option-icon {
  width: 22px;
  height: 28px;
  position: relative;
}

.option-icon::before,
.option-icon::after {
  content: "";
  position: absolute;
  background-color: #1d1d1d;
}

.option-text {
  color: var(--text-dark);
  font-size: 16px;
  opacity: 0.93;
}

.form-container {
  display: flex;
  flex-direction: column;
  gap: 53px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0;
}

.form-group label {
  color: var(--text-dark);
  font-size: 17px;
  font-weight: 500;
  opacity: 0.93;
  margin-bottom: 0;
}

.form-group input {
  height: 48px;
  border: 1px solid var(--gray-medium);
  border-radius: 5px;
  padding: 0 15px;
  font-size: 16px;
  outline: none;
}

.form-group input::placeholder {
  color: #d2d2d2;
}

.price-card {
  position: absolute;
  width: 248px;
  height: 121px;
  background-color: var(--white);
  border-radius: 24px;
  top: 262px;
  left: 382px;
  box-shadow: 30px 28px 81px rgba(0, 0, 0, 0.04);
  z-index: 3;
}

.price-card .price-content {
  margin: 8px;
  background: var(--accent-green);
  border-radius: 16px;
  height: 105px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 24px;
  gap: 9px;
}

.price-label {
  color: var(--primary-color);
  font-size: 17px;
  font-weight: 500;
  margin: 0;
}

.price-value {
  color: var(--primary-color);
  font-size: 32px;
  font-weight: 700;
  margin: 0;
} 